export default class Coupon {
  constructor(input) {
    Object.assign(this, input);
  }

  get c_name() {
    console.log();
    return `父类${this.name}`;
  }

  get strategy() {
    return [
      {
        function() {
          return this.isStart === "0";
        },
        btnName: "未开始",
        btnClassName: "grey",
      },
    ];
  }
}
