<template>
  <div>
    {{ `page${page}===${active}` }}
    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="load">
      <!-- 为了每次切换tab都重新加载数据 用v-if -->
      <van-cell
        v-for="(item, index) in normalizedList"
        :key="index"
        :title="`${item.c_name}-${item.rightsType}-${item.status}`"
        @click="handleCellClick(item)"
      />
    </van-list>
  </div>
</template>

<script>
import { Toast } from "vant";
import CouponFactory from "@/models/CouponFactory";
// import Coupon from "@/models/Coupon";
export default {
  name: "TabContent",
  props: {
    active: String,
  },
  data() {
    return {
      list: [],
      page: 1,
      size: 10,
      loading: false,
      finished: false,
    };
  },
  watch: {},
  computed: {
    normalizedList() {
      return this.list.map((n) => CouponFactory.create(n));
      // return this.list.map((n) => Coupon.create(n));
    },
  },
  created() {},
  methods: {
    sleep(second = 1) {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve();
        }, second * 1000);
      });
    },
    async load() {
      console.log("load....");
      // return;
      Toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true,
        message: "加载中",
      });
      await this.sleep(2);
      Toast.clear();
      for (let i = 0; i < 10; i++) {
        this.list.push({ rightsType: "CP", rightsName: "红包", status: "1" });
      }
      this.page++;
      this.loading = false;
      // 数据全部加载完成
      if (this.list.length >= 40) {
        this.finished = true;
      }
    },
    handleCellClick(item) {
      this.$router.push({
        name: "detail",
        query: {
          id: item,
        },
      });
    },
  },
};
</script>

<style lang="scss" scoped></style>
