import CP from "./CP";
import Coupon from "./Coupon";

export default class CouponTactory {
  static create(conponInfo) {
    const CLASS_MAPPING = { CP };

    if (CLASS_MAPPING[conponInfo.rightsType]) {
      return new CLASS_MAPPING[conponInfo.rightsType](conponInfo);
    }

    console.warn(`没有权益类型为${conponInfo.rightsType}的类`);
    return new Coupon(conponInfo);
  }
}
