<template>
  <div>
    <van-tabs v-model="active" sticky>
      <van-tab v-for="({ name, path }, index) in cates" :key="index" :title="name" :to="path" :name="path">
        <tab-content :active="active" v-if="active === path"></tab-content>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import TabContent from "./src/TabContent.vue";
export default {
  components: { TabContent },
  name: "list",
  data() {
    return {
      cates: [
        {
          path: "/list/animation",
          name: "动画",
        },
        {
          path: "/list/yuanchuang",
          name: "原创",
        },
        {
          path: "/list/music",
          name: "音乐",
        },
        {
          path: "/list/live",
          name: "直播",
        },
      ],
      active: this.$route.path,
    };
  },
  created() {
    console.log("list===created", this.$route.query);
  },
  beforeRouteUpdate(to, from, next) {
    console.log("beforeRouteUpdate");
    this.active = to.path;
    next();
  },
  beforeRouteLeave(to, from, next) {
    if (to.name !== "detail") {
      this.$store.commit("setCachedComponents", []);
    }
    if (to.name === "detail") {
      this.$store.commit("setCachedComponents", ["list"]);
    }
    next();
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      if (from.name === "home") {
        vm.$store.commit("setCachedComponents", ["list"]);
      }
    });
  },
  activated() {},
};
</script>

<style lang="scss" scoped></style>
